body {
    background-color: #030303;
    width: 400px;
    color: white;
}

.card {
    width: 400px;
    height: 600px;
    background-color: rgb(25, 25, 25) !important;
    border: none;
    border-radius: 0;
    z-index: 2;
    color: #edf0f1;
}

.suggest {
    margin-left: -11px;
}

.form-control {
    border: 2px solid #eee;
    border-radius: 10px
}

.form-control:focus {
    box-shadow: none;
    border: 2px solid #039BE5
}

form .txt_field {
    position: relative;
    border-bottom: 2px solid #adadad;
    margin: 30px 0;
  }

  .txt_field input {
    width: 100%;
    padding: 0 5px;
    height: 40px;
    font-size: 16px;
    border: none;
    background: none;
    outline: none;
    color: #fff;
  }

  .txt_field label {
    position: absolute;
    top: 50%;
    left: 5px;
    color: #adadad;
    transform: translateY(-50%);
    font-size: 16px;
    pointer-events: none;
    transition: 0.5s;
  }

  .txt_field span::before {
    content: "";
    position: absolute;
    top: 40px;
    left: 0;
    width: 0%;
    height: 2px;
    color: blueviolet;
    transition: 0.5s;
  }

  .txt_field input:focus~label,
  .txt_field input:valid~label {
    top: -5px;
    color: blueviolet;
  }

  .txt_field input:focus~span::before,
  .txt_field input:valid~span::before {
    width: 100%;
  }

  .form {
    margin-top: 200px;
  }

  input[type="submit"] {
    width: 100%;
    height: 50px;
    border: 1px solid;
    background: #edf0f1;
    border-radius: 25px;
    font-size: 18px;
    color: blueviolet;
    font-weight: 700;
    cursor: pointer;
    outline: none;
  }

  input[type="submit"]:hover {
    color: rgb(6, 1, 10);
    transition: 0.5s;
  }

  .signup_link {
    margin: 30px 0;
    margin-left: 0;
    text-align: center;
    font-size: 16px;
    color: #666666;
  }

  .signup_link a {
    color: blueviolet;
    text-decoration: none;
  }

  .signup_link a:hover {
    text-decoration: underline;
  }

  .address {
    display: flex;
    border: 2px solid;
    border-radius: 20px;
    padding-left: 7px;
}

.fa-copy {
    margin-inline: 90px;
    align-self: center;
}

.nav-tabs {
    justify-content: space-around;
}

.seed {
    border: 2px solid grey;
    border-radius: 20px;
    margin-inline: 12px;
    margin-block: 8px;
}

.terms {
    font-size: 12px;
    text-decoration: none;
    color: #039BE5
}

.token {
    align-items: center;

}

.button {
    position: fixed;
    top: 550px;
    margin-left: 82px;
}

.confirm-button {
    width: 150px;
    border-radius: 10px
}

a {
    color: inherit !important ;
    text-decoration: none !important;
}

a:hover {
    color: inherit !important;
}